@import
'../../../styles/utils/variables',
'../../../styles/utils/mixins';

.container {
  width: 500px;
  height: 305px;

  display: flex;
  flex-direction: column;

  background-color: #ffffff;
  border-radius: 8px;
  border: 1px solid #A1C0E0;
  box-shadow: 4px 10px 2px rgba(42, 47, 66, 0.15);

  font-family: 'Roboto', sans-serif;

  z-index: 2;
  overflow: auto;
  overflow-x: hidden;

  @include respond-to('large') {
    width: 500px;
    height: 305px;
  }

  @include respond-to('medium') {
    width: 360px;
    height: 220px;
  }

  @include darkTheme {
    background: $black-blue;
  }
}

.header {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  padding: 0 30px 0 0;

  @include respond-to('medium') {
    padding: 0 20px 0 0;
  }
}

.header_text {
  margin-top: 25px;
  margin-left: 45px;
  margin-right: 45px;

  font-size: $header;
  line-height: 40px;
  color: #0f3971;

  @include respond-to('large') {
    font-size: $headerLarge;
  }

  @include respond-to('medium') {
    margin-left: 30px;

    font-size: $headerMedium;
    line-height: 19px;
  }

  @include darkTheme {
    color: $grey;
  }
}

.close_icon {
  margin-right: 30px;
  width: 24px;
  height: 24px;
  margin-top: 32px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 14px;
    height: 14px;

    margin-top: 28px;
  }
}

.data_container {
  font-weight: 300;

  margin-top: 22px;

  padding-right: 45px;
  padding-left: 45px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;


  font-size: 17px;

  @include respond-to('medium') {
    padding-right: 30px;
    padding-left: 30px;

    font-size: 10px;
  }

  @include darkTheme {
    color: $white;
  }
}

.data_action_button {
  margin-top: 50px;
  margin-bottom: 10px;
  width: 150px;
  height: 45px;
  margin-left: 15px;

  line-height: 24px;

  @include respond-to('medium') {
    margin-top: 30px;
    width: 110px;
    height: 30px;

    line-height: 20px;
  }
}

.data_action_button span {
  font-size: 16px;
  line-height: 14px;

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.data_button_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  padding-left: 45px;
  padding-right: 45px;

  @include respond-to('medium') {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.search {
  margin-top: 16px;
  width: 100%;
  height: 25px;
}

.searchError {
  margin-top: 16px;
  width: 100%;
  height: 25px;
  border-color: #FF6161;
}

.error {
  color: #FF6161;
  margin-top: 10px;
}
